import { CButton, CCard, CCardBody, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { DataTable } from 'src/components';

const BrandList = () => {
  const [category, setCategory] = useState([]);


  useEffect(() => {

    var requestOptions = {
      method: 'POST',
      redirect: 'follow'
    };

    let url =  "http://localhost:5000/api/category/list"
    fetch(url, requestOptions)
      .then(response => response.json())
      .then(result => { console.log(result.data); setCategory(result.data) })
      .catch(error => console.log('error', error));

  }, []);

  const tableHeaders = [
                        { name: 'Id', field: '_id', short: true,  width:200}, 
                        { name: 'Name', field: 'name', short: true, width:200}, 
                        { name: 'Image', field: 'image', short: false, type: 'image'},
                        { name: 'Action', field: '_id', short: false, width:100}
                      ];

  return (
    <>
      <div className='d-flex justify-content-between  align-items-center  mb-4'>
        <h1 className='headingType1'>Category Listing</h1>
        <CButton color='success'>Add</CButton>
      </div>

      <CCard>
        <CCardBody>
          {category && <DataTable data={category} headers={tableHeaders} />}



        </CCardBody>
      </CCard>
    </>
  )
}

export default BrandList
